/* eslint-disable camelcase */
import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useEffect, useMemo, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm} from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Typography  } from '@mui/material';

// routes
import { PATH_ADMIN } from '../../routes/paths';
import { FormProvider, RHFSelect, RHFSwitch, RHFTextField } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

StoreDataCard.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
export default function StoreDataCard({ isEdit, currentUser }) {
  const { enqueueSnackbar } = useSnackbar();
  const [network, SetNewtork] = useState();
  const [plan, SetPlan] = useState([]);
  const network_list = ['MTN','AIRTEL','9MOBILE','GLO']
  const NewUserSchema = Yup.object().shape({
    pin: Yup.string().required('Data Pin Required'),
    data_card_id: Yup.string().required('plan Type required')
  });
  const EditUserSchema = Yup.object().shape({
    pin: Yup.string().required('Data Pin Required'),
    data_card_id: Yup.string().required('plan Type required'),
    serial: Yup.string().required()
  });
  const defaultValues = useMemo(
    () => ({
     network: currentUser?.network || '',
      serial: currentUser?.serial || '',
      pin: currentUser?.pin || '',
      plan_status: currentUser?.plan_status || false,
      plan_id: currentUser?.id || false,
      data_card_id: currentUser?.data_card_id || ''
    }),
    [currentUser]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(!isEdit? NewUserSchema : EditUserSchema),
    // defaultValues,
  });

  const {
    reset,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
      SetNewtork(currentUser?.network)
      GetPlan(currentUser?.network)
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);
 const values = watch();
  const onSubmit = async (data) => {
     try{
    await axios.post(`/api/add_store_data_card/${AccessToken}/secure`,{
        pin: data.pin,
        serial: data.serial,
        plan_status: data.plan_status,
        generate_serial: data.generate_serial,
        data_card_id: data.data_card_id,
        plan_id: data.plan_id,
        network
     });
     enqueueSnackbar('Create success!');
     navigate(PATH_ADMIN.plan.store_data_card);
     }catch(error){
        if(error.data?.message !== undefined){
            enqueueSnackbar(error.data?.message, { variant: 'error' })  
             } else if(error?.message !== undefined){
                enqueueSnackbar(error?.message, { variant: 'error' })   
             }else{
                enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
             }
     }
  }
  const onCreateReset = async (data) => {
try{
    await axios.post(`/api/add_store_data_card/${AccessToken}/secure`,{
        pin: data.pin,
        serial: data.serial,
        plan_status: data.plan_status,
        generate_serial: data.generate_serial,
        data_card_id: data.data_card_id,
        plan_id: data.plan_id,
        network
     });
reset();
enqueueSnackbar('Create success!');
}catch(error){
if(error.data?.message !== undefined){
    enqueueSnackbar(error.data?.message, { variant: 'error' })  
     } else if(error?.message !== undefined){
        enqueueSnackbar(error?.message, { variant: 'error' })   
     }else{
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
     }
}
  }

  const onEdit = async(data)=>{
try{
await axios.post(`/api/edit_store_data_plans/${AccessToken}/secure`,{
    pin: data.pin,
    serial: data.serial,
    plan_status: data.plan_status,
    generate_serial: data.generate_serial,
    data_card_id: data.data_card_id,
    plan_id: data.plan_id,
    network
 })
navigate(PATH_ADMIN.plan.store_data_card);
enqueueSnackbar('Update success!');
}catch(error){
if(error.data?.message !== undefined){
    enqueueSnackbar(error.data?.message, { variant: 'error' })  
     } else if(error?.message !== undefined){
        enqueueSnackbar(error?.message, { variant: 'error' })   
     }else{
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
     }
}
  }

  const onEditReset = async(data) => {
    try{
await axios.post(`/api/edit_store_data_plans/${AccessToken}/secure`,{
   pin: data.pin,
   serial: data.serial,
   plan_status: data.plan_status,
   generate_serial: data.generate_serial,
   data_card_id: data.data_card_id,
   plan_id: data.plan_id,
   network
})
navigate(PATH_ADMIN.plan.add_store_data_card);
enqueueSnackbar('Update success!');
        }catch(error){
        if(error.data?.message !== undefined){
            enqueueSnackbar(error.data?.message, { variant: 'error' })  
             } else if(error?.message !== undefined){
                enqueueSnackbar(error?.message, { variant: 'error' })   
             }else{
                enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
             }
        } 
  }

  const GetPlan = async(value)  =>  {
SetNewtork(value);
try{
const resp = await axios.post(`/api/get/data_card_plan/${AccessToken}/system`,{
    network: value
});
SetPlan(resp.data.plan)
}catch(_){
SetPlan([])
}
  }

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(!isEdit? onSubmit : onEdit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
                <RHFSelect name="network" label="Network" value={network} onChange={(value) => GetPlan(value.target.value)}>
                <option value="_" />
                {network_list?.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect name="data_card_id" label="Plan Type" defaultValue="_">
                <option value="" />
                {plan?.map((option) => (
                  <option key={option.plan_id} value={option.plan_id}>
                    {`${option.name}${option.plan_size} => ${option.plan_type} => ${option.plan_day}`}
                  </option>
                ))}
              </RHFSelect>
             
             {!isEdit && <><RHFTextField name="pin" label="Data Card Pin" multiline="true" rows="5"  defaultValue/>
              <RHFTextField name="serial" label="Data Card Serial Number" multiline ="true" rows="5"  defaultValue/>
              </>}
              {isEdit && <><RHFTextField name="pin" label="Data Card Pin"   defaultValue/>
              <RHFTextField name="serial" label="Data Card Serial Number"   defaultValue/>
              </>}
              <RHFSwitch
              name="plan_status"
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                   {values.plan_status? 'SOLD' : 'AVAILABLE'} 
                  </Typography>
                </>
              }
             
              sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
            />
            {!isEdit && <RHFSwitch
              name="generate_serial"
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                   {values.generate_serial? 'System Generating Serial Number' : 'Input Serial Number Manually'} 
                  </Typography>
                </>
              }
             
              sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
            />}
             
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 2, display: 'grid',
                columnGap: 1,
                rowGap: 2,  gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },}}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {!isEdit ? 'Create then Return ' : 'Save Changes then Return'}
              </LoadingButton>
               <LoadingButton type="button" onClick={handleSubmit(!isEdit? onCreateReset : onEditReset)} variant="contained" color="warning" loading={isSubmitting}>
               {!isEdit ? ' Create then Reset' : 'Save Changes then Create New'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
