import { useState, useEffect, } from 'react';
import { useParams, useLocation } from 'react-router-dom';
// @mui
import { Container,Backdrop,CircularProgress } from '@mui/material';
// routes
import { PATH_ADMIN} from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import StoreDataCard from '../../sections/admin/store_data_card';
import axios from '../../utils/axios';



export default function AddStoreCard(){
    const { themeStretch } = useSettings();
    const { pathname } = useLocation();
    const { name = '' } = useParams();
    const isEdit = pathname.includes('edit');
    const [currentUser, SetCurrentUser] = useState();
    const [load,SetLoad] = useState(true);
    const AccessToken =  window.localStorage.getItem('accessToken');
    useEffect(()  => {
        if(name){
        axios.post(`/api/r_add_store_data_card/${AccessToken}/secure`,{
            plan_id: name
        }).then(data => {
               SetCurrentUser(data.data?.plan); 
               SetLoad(false)
               }).catch(() => SetLoad(false));
            }else{
                SetCurrentUser([]);
                SetLoad(false)
            }
        }, [AccessToken, name, SetCurrentUser]);  
    return (
      <Page title={"Plan: " && !isEdit ? 'Add Data Card Pins' : 'Edit Data Card Pin'}>
        <Container maxWidth={themeStretch ? false : 'lg'}>
          <HeaderBreadcrumbs
            heading={!isEdit ? 'Add Data Card Pins' : `Edit Data Card Pin => Network ${!currentUser?.network ? '' : currentUser?.network}`}
            links={[
              { name: 'Dashboard', href: PATH_ADMIN.root },
              { name: 'Stock Data Card', href: PATH_ADMIN.plan.store_data_card },
              { name: !isEdit ? 'Network' : currentUser?.network },
            ]}
          />
  
          <StoreDataCard isEdit={isEdit} currentUser={currentUser} />
          <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
        </Container>
      </Page>
    );
}
